</template>

<script>

  import $ from 'jquery'
export default {
  name: 'others',
  data () {
    return {
      msg: 'this is from abouts.vue',
      list:"",
      aa:""
    }
  },
  created(){
    var that=this
          $.ajax({
     type: "get",
     url: 'http://m.edit.foxitreader.cn/api/getWebExeDownload',
     dataType: "jsonp",
     jsonp: 'jsonpcallback',
     success: function (data) {

     that.aa=data.descr

     }

     });
  },
  methods: {
    clickFn () {
      alert('about')
    },

    clickFns () {
      var that=this
      $.ajax({
        type: "get",
        url: 'http://m.edit.foxitreader.cn/api/getWebExeDownload',
        dataType: "jsonp",
        jsonp: 'jsonpcallback',
        success: function (data) {

          that.list=data.id

        }

      });

      console.log(2)
      /*$.ajax({
        url: "https://wechatmp.foxitreader.cn/api/miniProgramLogin_foxit",
        context: document.body,
        success: function(){
        $(this).addClass("done");
      }});*/
    }
  }
}
</script>

<style lang='scss' scoped>
.others {
}
</style>



