import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import abouts from '@/pages/abouts'
import others from '@/pages/others'
import box from '@/pages/box'
import abox from '@/pages/abox'
import bbox from '@/pages/bbox'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },

    {
      path: '/abouts',
      name: 'abouts',
      component: abouts
    },
    {
      path: '/others',
      name: 'others',
      component: others
    },
    {
      path: '/box',
      name: 'box',
      component:box,
      children:[
        {
          path:'/box',
          component:box,
        },
        {
          path:'/box/abox',
          component:abox,
        },
      {
        path:'/box/bbox',
        name:"bbox",
        component:bbox,
      }
    ]
    //children这是嵌套的部分
    }

  ]
})



